.elearning-home-section-1 {
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--tc-system-green-3);
        z-index: -1;
        border-radius: 0 !important;
    }
    .banner-girl {
        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 805px;
            height: 805px;
            border-radius: 805px;
            background: #003930;
            z-index: -1;
        }
    }
    .input-group {
        .btn-yellow {
            position: relative;
            ::before {
                content: '';
                position: absolute;
                width: 1px;
                height: 70%;
                top: 50%;
                background: var(--tc-system-stroke);
                transform: translateY(-50%);
