}
.progress,
.progress-stacked {
    --bs-progress-height: 4px;
    --bs-progress-font-size: 0.75rem;
    --bs-progress-bg: #e9e9e9;
    --bs-progress-border-radius: 20px;
    --bs-progress-box-shadow: var(--bs-box-shadow-inset);
    --bs-progress-bar-color: #fff;
    --bs-progress-bar-bg: var(--tc-theme-primary);
    --bs-progress-bar-transition: width 0.6s ease;
    display: flex;
    height: var(--bs-progress-height);
    overflow: hidden;
    font-size: var(--bs-progress-font-size);
    background-color: var(--tc-system-white);
    border-radius: var(--bs-progress-border-radius);
}
.tag-process {
    .icon-shape {
        transform: rotate(90deg);
    }
    @media (min-width: 1400px) {
        .icon-shape {
            transform: rotate(0deg);
        }
    }
}
.section-portfolio-2.accordion {
    justify-content: center;
    align-content: center;
    position: relative;
    top: 0;
    left: 5vw;
    transition: all 200ms ease-in-out 50ms;
    width: 90vw;
    opacity: 0;
    gap: 30px;
    .accordion-item {
        border-radius: 10px !important;
        border: 1px solid var(--tc-system-stroke);
        background: none;
        cursor: pointer;
        display: flex;
        @media (max-width: 767.98px) {
            flex-direction: column;
        }
        .accordion-link {
            background-color: var(--tc-system-white);
            transition: all 0.2s linear;
            .accordion-link-item {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: start;
                gap: 12px;
                .icon {
                    position: relative;
                    width: 44px;
                    height: 44px;
                    border-radius: 10px;
                    border: 1px solid var(--tc-system-stroke);
                    background: var(--tc-system-white);
                    transition-duration: 0.3s;

                    svg {
                        height: 14px;
                        width: 14px;
                        fill: var(--tc-theme-primary);
                    }
                    &:hover {
                        background: var(--tc-theme-primary);
                        border: 1px solid var(--tc-theme-primary);

                        svg {
                            fill: var(--tc-system-green-3);
                            transition-duration: 0.3s;
                        }
                    }
                }
            }
        }
        .accordion-img {
            height: 100%;
            background-position: bottom center;
            background-size: contain;
            background-repeat: no-repeat;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                background: url(../imgs/pages/learning/page-home/home-section-5/logo-bg.png);
                background-position: top center;
                background-size: cover;
                background-repeat: no-repeat;
            }
        }
    }
    .default {
        height: 360px;
        width: 300px;
        overflow: hidden;
        .accordion-link {
            width: 0;
            .accordion-link-item {
                transition: all 0.5s linear;
                height: 0;
                opacity: 0;
            }
        }
        .accordion-img {
            width: 100%;
        }
    }
    .expand {
        height: 360px;
        width: 630px;
        .accordion-link {
            width: 50%;
            padding: 0 40px;
            .accordion-link-item {
                transition: all 0.5s linear;
                height: 100%;
                opacity: 1;
            }
        }
        .accordion-img {
            width: 50%;
        }
    }
    @media (max-width: 991px) {
        .default {
            height: 300px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 360px;
            width: 100%;
            .accordion-link {
                width: 50%;
                padding: 0 40px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;
                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 50%;
            }
        }
    }
    @media (max-width: 767.98px) {
        .default {
            height: 250px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 550px;
            width: 100%;
            .accordion-link {
                width: 100%;
                padding: 20px 20px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;
                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
    }
}
