// instructor
.card-team-conference {
    .team-overlay {
        content: '';
        position: absolute;
        transition: all 0.3s linear;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: var(--tc-theme-primary);
        opacity: 0;
        transform: translateX(-50%);
        .icon {
            position: relative;
            width: 34px;
            height: 34px;
            border-radius: 34px;
            svg {
                height: 14px;
                width: 14px;
                fill: var(--tc-system-white);
                opacity: 0.5;
            }
            &:hover {
                background: var(--tc-system-white);
                svg {
                    fill: var(--tc-theme-primary);
                    opacity: 1;
                    transition-duration: 0.3s;
                }
            }
        }
    }
    &:hover {
        .team-overlay {
            opacity: 1;
            transform: translateY(0%);
        }
    }
}
